  border-radius: 50%;
  -webkit-animation: pulse infinite 2s;
          animation: pulse infinite 2s;
}

@-webkit-keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 $danger;
  }
  30% {
    -webkit-box-shadow: 0 0 0 0 $danger;
  }
  50% {
    -webkit-box-shadow: 0 0 2px 4px rgba(246,92,118, 0.4);
  }
  90% {
    -webkit-box-shadow: 0 0 5px 10px rgba(246,92,118, 0);
  }
  100% {
    -webkit-box-shadow: 0 0 0 0 rgba(246,92,118, 0);
  }
}
@keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 $danger;
  }
  20% {
    box-shadow: 0 0 0 0 $danger;
  }
  50% {
      box-shadow: 0 0 2px 4px rgba(246,92,118, 0.4);
  }
  90% {
      box-shadow: 0 0 5px 10px rgba(246,92,118, 0);
  }
  100% {
      box-shadow: 0 0 0 0 rgba(246,92,118, 0);
  }
}

